@keyframes success {
    0% {
        background: none; }
    50% {
        background: rgba(161, 240, 69, .7); }
    100% {
        background: none; } }

@keyframes error {
    0% {
        background: none; }
    50% {
        background: rgba(240, 87, 108, .7); }
    100% {
        background: none; } }

.item {
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 5px 10px;
    margin-bottom: 20px;
    cursor: pointer;
    transition: background .2s;

    &:last-child {
        margin-bottom: 0; }

    &:hover {
        background: rgba(255,255,255,.2); } }

.success {
    animation: success 1s; }

.error {
    animation: error 1s; }
