.all {
    width: 100%;
    display: flex;
    flex-grow: 1;
    justify-content: center;
    padding-top: 7%;
    background: linear-gradient(90deg, #fd8355 0%, #f0576c 37%, #f79cbd 100%); }

.title {
    text-align: center;
    margin-bottom: 40px; }

.wrapper {
    position: relative;
    width: 600px; }

.item {
    list-style: none;
    margin: 0;
    margin-top: 20px;
    padding: 0;

    &>a {
        text-decoration: none;
        color: inherit;
        transition: color .3s;

        &:hover {
            color: #363f54; } } }

.empty {
    display: block;
    text-align: center; }
